import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Container, Row, Col, Card, Button, Modal, Pagination, Footer } from 'react-bootstrap';
import Carousel from 'react-bootstrap/Carousel';
import CutomNavbar from './Navbar';

import logo from '../images/logo.jpg';
import slider1 from '../images/blue.jpg';
import slider2 from '../images/green.jpg';
import slider3 from '../images/red.jpg';
const News = () => {
    const [articles, setarticles] = useState([]);
    const [country, setCountry] = useState('us');
    const [category, setCategory] = useState('general');
    const [currentPage, setCurrentPage] = useState(1);
    const [articlesPerPage] = useState(8);
    const [selectedArticle, setSelectedArticle] = useState(null);

    useEffect(() => {
        const fetchNews = async () => {
            try {
                const response = await axios.get(`https://newsapi.org/v2/top-headlines`, {
                    params: {
                        country: country,
                        category: category,
                        apiKey: '209dd1b142a746b3992e2cc28070379c' // Replace Width your  api key  //
                    }
                });
                setarticles(response.data.articles);
            } catch (error) {
                console.error("Error fetching the News", error);
            }
        };

        fetchNews();
    }, [country, category]);
    // pagination logic
    const indexOfLastArticle = currentPage * articlesPerPage;
    const indexOfFirstArticle = indexOfLastArticle - articlesPerPage;
    const currentArticles = articles.slice(indexOfFirstArticle, indexOfLastArticle);

    const paginate = (pageNumber) => setCurrentPage(pageNumber);

    return (
        <Container fluid>

            <div className='my-4'>
                <CutomNavbar setCountry={setCountry} setCategory={setCategory} />
            </div>
            <div className="container-fluid">
                <Carousel>
                    <Carousel.Item>
                        <img src={slider1}
                            class="img-fluid" alt={slider1}
                        />
                        <Carousel.Caption>
                            {/* <div className="bannersliderone">
                                <h3>First slide label</h3>
                                <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                            </div>   */}
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={slider2}
                            class="img-fluid" alt={slider2}
                        />


                    </Carousel.Item>
                    <Carousel.Item>
                        <img src={slider3}
                            class="img-fluid" alt={slider3}
                        />
                        <Carousel.Caption>
                            {/* <h3>Third slide label</h3>
                            <p>
                                Praesent commodo cursus magna, vel scelerisque nisl consectetur.
                            </p> */}
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>



            </div>






            <Row>
                {currentArticles.map((article, index) => (
                    <Col key={index} sm={12} md={6} lg={3} className='mb-4'>
                        <Card onClick={() => setSelectedArticle(article)} className='mt-5'>
                            <Card.Img variant="top" src={article.urlToImage || 'https://via.placeholder.com/100'} />
                            <Card.Body>
                                <Card.Title>{article.title}</Card.Title>

                                {/* <Button variant="primary" href={article.url} target='_blank' rel='noopener noreferrer'>Read More</Button> */}
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>

            <Pagination>
                {Array.from({ length: Math.ceil(articles.length / articlesPerPage) }, (_, i) => (
                    <Pagination.Item key={i + 1} active={i + 1 === currentPage} onClick={() => paginate(i + 1)}>
                        {i + 1}
                    </Pagination.Item>
                ))}

            </Pagination>

            {selectedArticle && (
                <Modal show={true} onHide={() => setSelectedArticle(null)}>
                    <Modal.Header closeButton>
                        <Modal.Title>{selectedArticle.title}</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <img src={selectedArticle.urlToImage || 'https://via.placeholder.com/150'} alt={selectedArticle.title} className='img-fluid mb-3' />
                        <p>{selectedArticle.content}</p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setSelectedArticle(null)}>
                            Close
                        </Button>
                        <Button variant="primary" href={selectedArticle.url} target='_blank' rel='noopener noreferrer'>
                            Read More
                        </Button>
                    </Modal.Footer>
                </Modal>
            )}
            <div className='row fluid'>

                <footer class="text-center text-lg-start bg-body-tertiary text-muted">

                    <section class="d-flex justify-content-center justify-content-lg-between p-4 border-bottom">

                        <div class="me-5 d-none d-lg-block text-center">
                            <span> News World</span>
                        </div>

                        <div>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-facebook-f"></i>
                            </a>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-twitter"></i>
                            </a>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-google"></i>
                            </a>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-instagram"></i>
                            </a>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-linkedin"></i>
                            </a>
                            <a href="" class="me-4 text-reset">
                                <i class="fab fa-github"></i>
                            </a>
                        </div>

                    </section>



                    <section class="">
                        <div class="container text-center text-md-start mt-5">

                            <div class="row mt-3">

                                <div class="col-md-3 col-lg-4 col-xl-3 mx-auto mb-4">

                                    <h6 class="text-uppercase fw-bold mb-4">
                                        <i class="fas fa-gem me-3"></i>Company name
                                    </h6>
                                    <p>
                                        Newspapers and magazines that provide written news articles, either in physical print or digital form. Examples include
                                        The New York Times and The Guardian.Broadcast Media: These companies distribute news via television and radio broadcasts
                                        . Examples include CNN, BBC, and NPR. Digital-Only News: Many modern news outlets are purely digital, relying on  ebsites
                                        and social media for distribution.
                                    </p>
                                </div>

                                <div class="col-md-2 col-lg-2 col-xl-2 mx-auto mb-4">

                                    <h6 class="text-uppercase fw-bold mb-4">
                                        Countries News
                                    </h6>
                                    <p>
                                        <a href="#!" class="text-reset"> United States </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> India </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> United Kingdom</a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> Canada</a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset">Australia  </a>
                                        <a href="#!" class="text-reset">Germany  </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset">Germany  </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> France  </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset">  Italy  </a>
                                    </p>

                                </div>

                                <div class="col-md-3 col-lg-2 col-xl-2 mx-auto mb-4">

                                    <h6 class="text-uppercase fw-bold mb-4">
                                        Useful  Category News
                                    </h6>
                                    <p>
                                        <a href="#!" class="text-reset"> General </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> Business </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> Entertainment</a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> Health</a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset"> Science</a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset">  Sports </a>
                                    </p>
                                    <p>
                                        <a href="#!" class="text-reset">   Technology </a>
                                    </p>



                                </div>

                                <div class="col-md-4 col-lg-3 col-xl-3 mx-auto mb-md-0 mb-4">

                                    <h6 class="text-uppercase fw-bold mb-4">Contact</h6>
                                    <p><i class="fas fa-home me-3"></i> Sultanpur New Delhi </p>
                                    <p>
                                        <i class="fas fa-envelope me-3"></i>
                                        MannuKumar74000@gmail.com
                                    </p>
                                    <p><i class="fas fa-phone me-3"></i> +918809823983</p>
                                    <p><i class="fas fa-print me-3"></i> +918809823983</p>
                                </div>

                            </div>

                        </div>
                    </section>

                    <div class="text-center p-4">
                        © 2024 Copyright:
                        <a class="text-reset fw-bold" href="https://mannukumar.com">MannuKumar.com</a>
                    </div>

                </footer>


            </div>

        </Container>
    );
};

export default News;